@charset "ISO-8859-1";

a {
  text-decoration: none;
}

#check-new-mess {
  cursor: pointer;
}

hr {
  margin-top: 1rem;
  margin-bottom: 1rem;
  border: 0;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}

#all-alert {
  position: fixed;
  top: 10%;
  right: 20px;
  z-index: 1;
}

.col-form-label {
  font-size: 15px;
}

/*
 * chatbox
 */

#wrapper {
  margin: 5px auto;
}

body {
  font-family: Arial;
  background: #fff;
}

.chat ul {
  margin: 0px;
  padding: 0px;
  list-style: none;
}

.message-left .message-name {
  display: block;
  font-size: 12px;
  text-align: left;
  padding-left: 10px;
  padding-top: 4px;
  color: #868e96;
}

.message-right .message-name {
  display: block;
  font-size: 12px;
  text-align: right;
  padding-right: 10px;
  padding-top: 4px;
  color: #868e96;
}

.message-left .message-time {
  font-size: 12px;
  text-align: left;
  padding-left: 4px;
  color: #ccc;
  font-family: Courier;
}

.message-right .message-time {
  display: inline;
  font-size: 12px;
  text-align: right;
  padding-right: 4px;
  color: #ccc;
  font-family: Courier;
}

.message-left {
  text-align: left;
  margin-bottom: 2px;
}

.message-left .message-text {
  max-width: 80%;
  display: inline-block;
  background: #fff;
  padding: 5px 15px;
  font-size: 15px;
  color: #999;
  border-radius: 7px 30px 30px 7px;
  font-weight: 100;
  line-height: 1.5em;
  max-width: 80%;
}

.message-right {
  text-align: right;
  margin-bottom: 2px;
}

.message-right .message-text {
  line-height: 1.5em;
  display: inline-block;
  background: #5ca6fa;
  padding: 5px 15px;
  font-size: 15px;
  color: #fff;
  border-radius: 30px 7px 7px 30px;
  line-height: 1.5em;
  font-weight: 100;
  text-align: left;
  max-width: 80%;
}

.chat {
  border-radius: 30px;
  padding: 12px;
  background: #f5f7fa;
}

.chat-container {
  height: 400px;
  overflow-y: scroll;
  padding-right: 16px;
}

.spinme-right {
  display: inline-block;
  padding: 15px 20px;
  font-size: 14px;
  border-radius: 30px;
  line-height: 1.25em;
  font-weight: 100;
  opacity: 0.2;
}

.spinme-left {
  display: inline-block;
  padding: 15px 20px;
  font-size: 14px;
  color: #ccc;
  border-radius: 30px;
  line-height: 1.25em;
  font-weight: 100;
  opacity: 0.2;
}

.spinner {
  margin: 0;
  width: 30px;
  text-align: center;
}

.spinner > div {
  width: 10px;
  height: 10px;
  border-radius: 100%;
  display: inline-block;
  -webkit-animation: sk-bouncedelay 1.4s infinite ease-in-out both;
  animation: sk-bouncedelay 1.4s infinite ease-in-out both;
  background: rgba(0, 0, 0, 1);
}

.spinner .bounce1 {
  -webkit-animation-delay: -0.32s;
  animation-delay: -0.32s;
}

.spinner .bounce2 {
  -webkit-animation-delay: -0.16s;
  animation-delay: -0.16s;
}

.message-left.start .message-text {
  border-radius: 30px 30px 30px 7px;
}

.message-left.end .message-text {
  border-radius: 7px 30px 30px 30px;
}

.message-right.start .message-text {
  border-radius: 30px 30px 7px 30px;
}

.message-right.end .message-text {
  border-radius: 30px 7px 30px 30px;
}

.startend .message-text {
  border-radius: 30px !important;
}

li.new {
  margin-top: 20px;
}

@-webkit-keyframes sk-bouncedelay {
  0%,
  80%,
  100% {
    -webkit-transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
  }
}

@keyframes sk-bouncedelay {
  0%,
  80%,
  100% {
    -webkit-transform: scale(0);
    transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
    transform: scale(1);
  }
}
